.Container {
    padding-left: 6%;
    padding-right: 6%;
}

.Images {
    display: flex;
    justify-content: space-between;
}

img {
    margin-top: 1%;
    margin-bottom: 3%;
    width: 32.5%;
    height: 70vh;
    object-fit: cover;
}

h3 {
    text-decoration: line-through;
    text-decoration-color: #f5bd07;
    text-decoration-thickness: 5%;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 6.5vw;
    text-align: center;
    height: 30vh;
}

.Product {
    display: flex;
    flex-wrap: wrap;
}