.Header {
    padding: 2%;
    padding-left: 6%;
    padding-right: 6%;
    overflow: hidden;
}

.CartIcon {
    font-size: 2vw;
    vertical-align: middle;
}

.SearchIcon {
    font-size: 1.5vw;
    color: grey;
    margin-left: 2%;
    vertical-align: middle;
}

.HeaderPages {
    text-decoration: 0;
    padding-right: 1.8%;
    font-size: 1.2vw;
    vertical-align: middle;
    color: black;
    transition: color 0.5s;

    &:hover {
        color: gold;

    }
}

.Search {
    border: 0;
    font-size: 1.2vw;
    margin-right: 20%;
    vertical-align: middle;

    &:focus{
        outline: 0;
    }

    &:hover{
        border-bottom: 1px solid black;
        outline: 0;
    }
}

.LogoButton {
    background-color: black;
    font-weight: bold;
    color: white;
    padding: 1%;
    font-size: 1.5vw;
    cursor: pointer;
    transition: color 1s;

    &:hover{
        color: gold;
    }
}